<template>
  <div style="padding: 10px;">
    <div style="float: left;">
      <Select :loading="searching"
              v-model="addUser"
              remote
              filterable
              :remote-method="searchUser"
              size="small">
        <Option v-for="item in userList" :value="item.account" :key="item.id">{{ item.account }}</Option>
      </Select>
    </div>
    <i-button type="primary" @click="subAddUser" size="small" style="margin: 0 10px;">添加成员</i-button>
    <Table :columns="columns" :data="userTableData" style="margin-top: 10px;"></Table>
  </div>
</template>
<script>
  import getData from '../config/getData'
  import { getUser } from '../config/data'
  export default {
    created () {
      getUser().then(user => {
        this.user = user
      })
      this.searchTeamItem()
    },
    data () {
      return {
        loginUserRole: null,
        user: null,
        searching: false,
        userList: [],
        addUser: null,
        columns: [
          {
            title: '成员',
            key: 'account',
            align: 'center'
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, { row }) => {
              if (row.id === this.user.id) {
                return
              }
              console.log(this.loginUserRole)
              if (this.loginUserRole !== 1) {
                return
              }
              return h('div', [
                h('Poptip', {
                  props: {
                    title: '您确认移除该用户？',
                    confirm: true
                  },
                  on: {
                    'on-ok': () => {
                      console.log(this)
                      this.deleteUser(row)
                    }
                  }
                }, [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    }
                  }, '移除')
                ]),
                h('Poptip', {
                  props: {
                    title: '您确认转让团队给该用户？',
                    confirm: true
                  },
                  on: {
                    'on-ok': () => {
                      console.log(row)
                    }
                  }
                }, [
                  h('Button', {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    }
                  }, '转让团队')
                ])
              ])
            }
          }
        ],
        userTableData: []
      }
    },
    methods: {
      searchTeamItem () {
        getData('teamUserSearch', { id: parseInt(this.$route.params.id) }).then(res => {
          for (let user of res.data.data) {
            console.log(user)
            if (user.id === this.user.id) {
              this.loginUserRole = user.roleId
            }
          }
          this.userTableData = res.data.data
        })
      },
      deleteUser ({ id }) {
        getData('teamUserDelete', {
          userId: id,
          teamId: parseInt(this.$route.params.id)
        }).then(() => {
          this.searchTeamItem()
          this.$Message.success('删除成功')
        }, () => {
          this.$Message.error('删除失败')
        })
      },
      subAddUser () {
        for (let user of this.userList) {
          if (user.account === this.addUser) {
            getData('teamUserAdd', {
              userId: parseInt(user.id),
              teamId: parseInt(this.$route.params.id)
            }).then(() => {
              this.addUser = ''
              this.searchTeamItem()
              this.$Message.success('添加成功')
            }, () => {
              this.$Message.error('添加失败')
            })
            continue
          }
        }
      },
      searchUser (userName) {
        if (!userName) {
          return
        }
        this.searching = true
        getData('searchUser', { userName }).then(res => {
          this.searching = false
          this.userList = res.data.data
        }, () => {
          this.searching = false
        })
      }
    }
  }
</script>



